<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="80px"
    >
      <el-row>
                  <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="mingcheng">
          <el-input v-model="ruleForm.mingcheng" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.mingcheng" label="" prop="mingcheng">
              <el-input v-model="ruleForm.mingcheng" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                                          <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="chuxingshijian">
          <el-input v-model="ruleForm.chuxingshijian" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.chuxingshijian" label="" prop="chuxingshijian">
              <el-input v-model="ruleForm.chuxingshijian" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="type!='info'"  label="" prop="xianzaizhuangkuang">
          <el-select v-model="ruleForm.xianzaizhuangkuang" placeholder="">
            <el-option
                v-for="(item,index) in xianzaizhuangkuangOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.xianzaizhuangkuang" label="" prop="xianzaizhuangkuang">
              <el-input v-model="ruleForm.xianzaizhuangkuang" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'" label="" prop="dengjiriqi">
            <el-date-picker
                format="yyyy  MM  dd "
                value-format="yyyy-MM-dd"
                v-model="ruleForm.dengjiriqi" 
                type="date"
                placeholder="">
            </el-date-picker> 
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.dengjiriqi" label="" prop="dengjiriqi">
              <el-input v-model="ruleForm.dengjiriqi" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="yonghuming">
          <el-input v-model="ruleForm.yonghuming" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.yonghuming" label="" prop="yonghuming">
              <el-input v-model="ruleForm.yonghuming" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="xingming">
          <el-input v-model="ruleForm.xingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.xingming" label="" prop="xingming">
              <el-input v-model="ruleForm.xingming" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="type!='info'"  label="" prop="shouji">
          <el-input v-model="ruleForm.shouji" 
              placeholder="" clearable></el-input>
        </el-form-item>
        <div v-else>
          <el-form-item v-if="ruleForm.shouji" label="" prop="shouji">
              <el-input v-model="ruleForm.shouji" 
                placeholder="" readonly></el-input>
          </el-form-item>
        </div>
      </el-col>
                        </el-row>
                                      <el-row>
            <el-col :span="24">
              <el-form-item v-if="type!='info'" label="" prop="chuxingdidian">
                <el-input
                  style="min-width: 200px; max-width: 600px;"
                  type="textarea"
                  :rows="8"
                  placeholder=""
                  v-model="ruleForm.chuxingdidian">
                </el-input>
              </el-form-item>
              <div v-else>
                <el-form-item v-if="ruleForm.chuxingdidian" label="" prop="chuxingdidian">
                    <span>{{ruleForm.chuxingdidian}}</span>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
                                                                                                                                                                                                                              <el-form-item>
                <el-button v-if="type!='info'" type="primary" @click="onSubmit"></el-button>
        <el-button v-if="type!='info'" @click="back()"></el-button>
        <el-button v-if="type=='info'" @click="back()"></el-button>
      </el-form-item>
    </el-form>
    
    
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";
export default {
  data() {
    let self = this
    var validateIdCard = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!checkIdCard(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateUrl = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isURL(value)) {
        callback(new Error("URL"));
      } else {
        callback();
      }
    };
    var validatePhone = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isMobile(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateEmail = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isEmail(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isNumber(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateIntNumber = (rule, value, callback) => {
      if(!value){
        callback();
      } else if (!isIntNumer(value)) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      id: '',
      type: '',
            ruleForm: {
                        mingcheng: '',
                                chuxingdidian: '',
                                chuxingshijian: '',
                                xianzaizhuangkuang: '',
                                dengjiriqi: '',
                                yonghuming: '',
                                xingming: '',
                                shouji: '',
                      },
                                                                  xianzaizhuangkuangOptions: [],
                                                                            rules: {
                  mingcheng: [
                            { required: true, message: '', trigger: 'blur' },
                                                                                              ],
                  chuxingdidian: [
                                                                                              ],
                  chuxingshijian: [
                                                                                              ],
                  xianzaizhuangkuang: [
                                                                                              ],
                  dengjiriqi: [
                                                                                              ],
                  yonghuming: [
                                                                                              ],
                  xingming: [
                                                                                              ],
                  shouji: [
                                                                                              ],
              }
    };
  },
  props: ["parent"],
  computed: {
                                                                                                      },
  methods: {
        // 
    download(file){
      window.open(`${file}`)
    },
    // 
    init(id,type) {
      if (id) {
        this.id = id;
        this.type = type;
      }
      if(this.type=='info'||this.type=='else'){
        this.info(id);
      }else if(this.type=='cross'){
        var obj = this.$storage.getObj('crossObj');
        for (var o in obj){
                    if(o=='mingcheng'){
            this.ruleForm.mingcheng = obj[o];
            continue;
          }
                    if(o=='chuxingdidian'){
            this.ruleForm.chuxingdidian = obj[o];
            continue;
          }
                    if(o=='chuxingshijian'){
            this.ruleForm.chuxingshijian = obj[o];
            continue;
          }
                    if(o=='xianzaizhuangkuang'){
            this.ruleForm.xianzaizhuangkuang = obj[o];
            continue;
          }
                    if(o=='dengjiriqi'){
            this.ruleForm.dengjiriqi = obj[o];
            continue;
          }
                    if(o=='yonghuming'){
            this.ruleForm.yonghuming = obj[o];
            continue;
          }
                    if(o=='xingming'){
            this.ruleForm.xingming = obj[o];
            continue;
          }
                    if(o=='shouji'){
            this.ruleForm.shouji = obj[o];
            continue;
          }
                  }
                                                                                                                                              }
            // 
      this.$http({
        url: `${this.$storage.get('sessionTable')}/session`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          var json = data.data;
                                                                                                                                                  this.ruleForm.yonghuming = json.yonghuming
                                                this.ruleForm.xingming = json.xingming
                                                this.ruleForm.shouji = json.shouji
                              } else {
          this.$message.error(data.msg);
        }
      });
                                                                                                this.xianzaizhuangkuangOptions = ",,".split(',')
                                                                                                    },
                                                                        // 
                                                                                            info(id) {
      this.$http({
        url: `xingdongguiji/info/${id}`,
        method: "get"
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.ruleForm = data.data;
        } else {
          this.$message.error(data.msg);
        }
      });
    },
        // 
    onSubmit() {
                  // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                              // ${column.compare}
                                                                                                                                                                                    this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `xingdongguiji/${!this.ruleForm.id ? "save" : "update"}`,
            method: "post",
            data: this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$message({
                message: "",
                type: "success",
                duration: 1500,
                onClose: () => {
                  this.parent.showFlag = true;
                  this.parent.addOrUpdateFlag = false;
                  this.parent.xingdongguijiCrossAddOrUpdateFlag = false;
                  this.parent.search();
                }
              });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    },
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    back() {
      this.parent.showFlag = true;
      this.parent.addOrUpdateFlag = false;
      this.parent.xingdongguijiCrossAddOrUpdateFlag = false;
    },
                                                                                                      }
};
</script>
<style lang="scss">
.editor{
  height: 500px;
}
.amap-wrapper {
  width: 100%;
  height: 500px;
}
.search-box {
  position: absolute;
}
</style>
